*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.UserDialog-Wrapper{ 
    /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
    /*内容居中*/  display: -ms-flexbox;  display: flex; -ms-flex-pack: center; justify-content: center; -ms-flex-align: center; align-items: center;
    background:-webkit-gradient(linear, left bottom, right top, from(rgba(38,50,105,0.5)), to(rgba(38,50,105,0.5))), url(./backimg/Neuschwanstein.jpg);
    background:-webkit-linear-gradient(left bottom, rgba(38,50,105,0.5), rgba(38,50,105,0.5)), url(./backimg/Neuschwanstein.jpg);
    background:-o-linear-gradient(left bottom, rgba(38,50,105,0.5), rgba(38,50,105,0.5)), url(./backimg/Neuschwanstein.jpg);
    background:linear-gradient(to right top, rgba(38,50,105,0.5), rgba(38,50,105,0.5)), url(./backimg/Neuschwanstein.jpg);

  }
  .UserDialog{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack:center;
        justify-content:center;
    border: 1px solid;
    border-radius: 4px;
    max-width: 440px;
    width: calc(100% - 40px);
    background: white;
    border: 1px solid #aaa;
    -webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
            box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
    padding: 44px;
}
.UserDialog .signInOrSignUp{
  width: 100%;
}
.UserDialog .signInOrSignUp > nav{
  display: -ms-flexbox;
  display: flex; 
  -ms-flex-pack: center; 
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.UserDialog .signInOrSignUp > nav:first-child{
  width: 100%;
  display: -ms-flexbox;
  display: flex; 
  -ms-flex-pack: center; 
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.7em;
}
.UserDialog .signInOrSignUp > nav:first-child label{padding: 0 15px;cursor: pointer;}
.UserDialog .signInOrSignUp > .panes { 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 1em 1em; }
.UserDialog .signInOrSignUp > nav .activetab
{border-bottom: 2px solid #0067b8;  
  color: #0067b8;
}
.UserDialog .forgotPassword > h3
 { padding: 1em; }
.UserDialog .forgotPassword > form 
{ padding: 0 1em 1em; }
 .UserDialog form>.row
 { display: -ms-flexbox; display: flex; padding: .3em 0; }
.UserDialog form>.row.actions{ 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-align: center;
      align-items: center;
   }
.UserDialog form p.display{ display: block; color: red; font-size: 8px}
.UserDialog form p.nondisplay{ display: none}
.UserDialog input[type = submit]{
  padding: 6px 20px;
  border: none;
  background: #0067b8;
  -webkit-box-shadow: 0px 0px 2px rgba(0,0,0,0.4);
          box-shadow: 0px 0px 2px rgba(0,0,0,0.4);
  color: #fff}
  .UserDialog input[type = text],
  .UserDialog input[type = password]{
    width: 100%;
    padding: 10px 10px 10px 28px;
    outline: none;
    border-top-width: 0; 
    border-left-width: 0; 
    border-right-width: 0;
    border-bottom: #000 solid 0.3px;
  }
  .UserDialog .signInOrSignUp .forgetpassword{
    font-size: .8125rem;
    text-decoration: none;
    color: #0067b8;
    display: block;
    margin: 5px 0px;
  }
  .UserDialog .panes label{
    display: block;
    position: relative;
    width: 100%;
  }
  .UserDialog .fas,  
  .UserDialog .far{
    position: absolute;
    color: #666;
    left: 7px;
    top: 13px;
    font-size: 15px;
  } 
  .UserDialog form{
    width: 90%;
    margin: 0 auto;
  }



